.app__wrapper {
  // main wrapper of the header
  .wavesHeader__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 36px 0px 36px;
    width: 100%;
    // height: 175px;
    height: 75px;
    // background: var(--saukko-main);
    text-align: center;
    position: absolute;
    gap: 10px;
    top: 0;

    button {
      position: absolute;
      padding: 58px 0px 0px 36px;
      background: none;
      border: none;
      left: 0;
      top: 0;
      z-index: 1;
    }

    h1 {
      font-size: 18px;
      font-weight: 400;
      //z-index: 1;
    }
    h2 {
      //z-index: 1; fixed for hamburger menu
      margin-top: 8px;
    }

    img {
      //z-index: 1; fixed for hamburger menu
      padding-top: 15px;
    }

    svg {
      width: 100%;
      height: auto;
      transform: scaleY(-1);
      // fill: var(--saukko-main);
      position: absolute;
      bottom: -25px;
      z-index: 0;
    }
  }
}
