.feedback {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 18px;
  padding: 35px 0;
}
.button-style {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 20px;
}
.feedback-para {
  font-size: 15px;
  color: #666;
  margin-bottom: 17px;
}
.list-style {
  width: 100%;
  height: 56px;
  display: flex;
  gap: 8px;
  align-items: center;
  margin-bottom: 8px;
}
