.app__wrapper {

  .confirmSelection__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-height: 100vh;

    .confirmSelection__container {
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-top: 200px;
      margin-bottom: 50px;
      width: 100%;
      flex-grow: 1;
      overflow-y: auto;

      .confirmSelection__infolist-wrapper {
        padding: 20px;

        .confirmSelection__infolist-item {
          padding: 10px 5px;

          .second__title {
            margin-bottom: 5px;
          }
        }

        .confirmSelection__infolist-item:nth-of-type(odd) {
          background-color: #F2F2F2;
        }
      }

      .confirmSelection__container--title {
        text-decoration: underline;
        font-size: 16px;
        text-align: center;
      }

      .confirmSelection__container--desc {
        margin-left: 15px;
        margin-right: 15px;
      }

      .Degree__confirmSelection__container--secondtitle{
        text-align: center;
        text-decoration: underline;
        font-size: 18px;
        padding: 20px;
        margin-bottom: 10px;
      }

      .confirmSelection__container--buttons {
        margin-top: auto;
        margin-bottom: 15px;
        margin-left: 15px;
        margin-right: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;

        .button__container {
          .button__icon{
            width: 25px;
            flex-shrink: 0;
          }

          .button__text {
            font-size: 15px;
          }
        }

        .confirmSelection__container--buttons-back {
          width: 40%;
          .button__container {
            flex-direction: row-reverse;
            color: var(--link-blue);
            background: var(--saukko-main-white);
            border: 2px solid var(--link-blue);
          }
        }

        .confirmSelection__container--buttons-forward {
          width: 60%;
          .button__container {
            background: var(--link-blue);
            color: var(--saukko-main-white);
            border: 2px solid var(--link-blue);
          }
        }
      }
    }

    /* Pop-up confirmation component styling */
    .popup__wrapper {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      /* Disabling the blur to make styling consistent with NotificationModal */
      /* backdrop-filter: blur(8px); */
      background-color: rgba(0, 0, 0, 0.6);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 9999;
    }
    
    .popup__container {
      background-color: white;
      border: none;
      width: 85%;
      padding: 20px;
      text-align: center;
      box-shadow: rgba(50, 50, 93, 0.4) 0px 25px 50px -12px, rgba(0, 0, 0, 0.5) 0px 10px 20px -10px;
    }
    
    .popup__container--title {
      font-size: 20px;
      margin-bottom: 10px;
    }
    
    .popup__container--text {
      margin-bottom: 20px;
    }
    
    .popup__container--buttons {
      display: flex;
      flex-direction: column;

      .button__container {
        margin-bottom: 10px;
      }
    }
    
    .popup__container--buttons-confirm {
      .button__container {
        background-color: var(--link-blue);
        border: none;
        color: var(--saukko-main-white);
      }
    }

    .popup__container--buttons-cancel {
      .button__container {
        background-color: var(--saukko-main-white);
        border: 2px solid var(--link-blue);
        color: var(--link-blue);
      }
    }
  }
}

.second__title{
  font-size: 16px;
  margin-left: 10px; 
  padding: 10px 0px 4px 0px;
  
}
.second__paragraph{
  font-size: 14px;
  margin-left: 14px;
} 